.borderdiv {
  border-radius: 10px;
  width: 90%;
  text-align: center;
  margin: auto;
  border: 1px solid black;
}
.borderdiv:hover {
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#edbbbd),
    color-stop(#eec5c9),
    color-stop(#efcfd4),
    color-stop(#f0dade),
    to(#f1e4e7)
  );
  background-image: -webkit-linear-gradient(
    top,
    #edbbbd,
    #eec5c9,
    #efcfd4,
    #f0dade,
    #f1e4e7
  );
  background-image: -o-linear-gradient(
    top,
    #edbbbd,
    #eec5c9,
    #efcfd4,
    #f0dade,
    #f1e4e7
  );
  background-image: linear-gradient(
    to bottom,
    #edbbbd,
    #eec5c9,
    #efcfd4,
    #f0dade,
    #f1e4e7
  );

  -webkit-transition: 0.7s;

  -o-transition: 0.7s;

  transition: 0.7s;
}



