@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: -webkit-gradient(linear, left top, right top, from(#ae1e51), color-stop(#c03969), color-stop(#d25181), color-stop(#e36899), to(#f47eb2));
  background-image: -webkit-linear-gradient(left, #ae1e51, #c03969, #d25181, #e36899, #f47eb2);
  background-image: -o-linear-gradient(left, #ae1e51, #c03969, #d25181, #e36899, #f47eb2);
  background-image: linear-gradient(to right, #ae1e51, #c03969, #d25181, #e36899, #f47eb2);
  font-family: 'Roboto Condensed', sans-serif;

}



